@import url('https://fonts.googleapis.com/css?family=Cabin|Cabin+Condensed|Cabin+Sketch|Inconsolata|Lobster|Lobster+Two|Roboto|Open+Sans');

.title {
  font-size: 2em;
  font-weight: 700;
  margin: 10px 0px;
  text-align: right;
}

.heading {
  font-size: 1.5em;
  font-weight: bold;
  margin: 10px 0px;
}
.image {
  width: auto;
  height: auto;
  overflow: hidden;
  margin-bottom: 20px;
}
.image img {
  max-height:100%; max-width:100%;
}
.image .caption {
  font-size: 0.8em;
  font-style: italic;
  color: gray;
  text-align: center;
}
.date {
  font-size: 0.8em;
  font-style: italic;
  text-align: right;
}